<template>
  <div class="wrapper">
    <NavBar back title="发起申请" />
    <div class="form">
      <van-form @submit="onSubmit">
        <van-cell-group>
          <van-field v-model="username" name="username" label="报修人" placeholder="请输入报修人" />
          <van-field v-model="idNumber" name="idNumber" label="学工号" placeholder="请输入学工号" />
          <van-field v-model="part" name="part" label="所属部分" placeholder="请输入所属部分" />
          <van-field v-model="phone" name="phone" label="联系方式" placeholder="请输入联系方式" />
          <van-field
            readonly
            clickable
            label="所属校区"
            name="schoolArea"
            :value="schoolArea"
            placeholder="选择校区"
            right-icon="arrow-down"
            @click="showSchoolPicker = true"
          />
          <van-popup v-model="showSchoolPicker" round position="bottom">
            <van-picker
              show-toolbar
              :columns="schools"
              @cancel="showSchoolPicker = false"
              @confirm="onSchoolConfirm"
            />
          </van-popup>
          <van-field
            readonly
            clickable
            label="报修分类"
            :value="area"
            name="area"
            placeholder="选择分类"
            right-icon="arrow-down"
            @click="showAreaPicker = true"
          />
          <van-popup v-model="showAreaPicker" round position="bottom">
            <van-picker
              show-toolbar
              :columns="areas"
              @cancel="showAreaPicker = false"
              @confirm="onAreaConfirm"
            />
          </van-popup>
          <van-field v-model="title" name="title" label="工单标题" placeholder="请输入工单标题" />
          <van-field v-model="description" name="description" label="报修内容" type="textarea" placeholder="请输入报修内容" />
          <van-field v-model="location" name="location" label="报修地点" placeholder="请输入报喜地点" />
          <van-field v-model="attachment" name="attachment" label="附件" placeholder="请选择附件">
            <template #input>
              <van-uploader v-model="attachment" multiple :after-read="afterRead" />
            </template>
          </van-field>
          <van-field
            readonly
            clickable
            label="分配班组"
            name="assignClass"
            :value="assignClass"
            placeholder="选择班组"
            right-icon="arrow-down"
            @click="showClassPicker = true"
          />
          <van-popup v-model="showClassPicker" round position="bottom">
            <van-picker
              show-toolbar
              :columns="classs"
              @cancel="showClassPicker = false"
              @confirm="onClassConfirm"
            />
          </van-popup>
          <van-field v-model="classMonitor" name="classMonitor" label="班组长" placeholder="请输入班组长" />
        </van-cell-group>
        <van-cell-group>
          <van-field name="approveStatus" label="审批状态">
            <template #input>
              <van-radio-group v-model="approveStatus" direction="horizontal">
                <van-radio name="1">通过</van-radio>
                <van-radio name="2">不通过</van-radio>
              </van-radio-group>
            </template>
          </van-field>

          <van-field v-model="approve" name="approve" label="审批意见" type="textarea" placeholder="请填写审批意见" />
        </van-cell-group>
        <div class="btn-group">
          <van-button type="info" native-type="submit">提交</van-button>
          <van-button plain type="danger">取消</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import {CellGroup, Field, RadioGroup, Radio, Uploader, Picker, Popup, Button, Form} from 'vant'
import NavBar from '@/components/NavBar'
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Field.name]: Field,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Uploader.name]: Uploader,
    [Picker.name]: Picker,
    [Popup.name]: Popup,
    [Button.name]: Button,
    [Form.name]: Form,
    NavBar
  },
  data() {
    return {
      username: '',
      idNumber: '',
      classMonitor: '',
      approve: '',
      title: '',
      description: '',
      location: '',
      part: '',
      assignClass: '',
      attachment: [],
      phone: '',
      approveStatus: '1',
      schoolArea: '',
      area: '',
      asignClass: '',
      showSchoolPicker: false,
      showAreaPicker: false,
      showClassPicker: false,
      schools: ['长安校区', '雁塔校区'],
      areas: ['家属区', '宿舍区'],
      classs: ['班组1', '班组2', '班组3'],
    }
  },
  methods: {
    onSchoolConfirm(value) {
      this.schoolArea = value;
      this.showSchoolPicker = false;
    },
    onAreaConfirm(value) {
      this.area = value;
      this.showAreaPicker = false;
    },
    onClassConfirm(value) {
      this.assignClass = value;
      this.showClassPicker = false;
    },
    afterRead(file) {
      file.status = 'uploading';
      file.message = '上传中...';

      console.log(file);//file文件如下图
      let formData = new FormData(); //构造一个 FormData，把后台需要发送的参数添加
      formData.append('file', file.file); //接口需要传的参数
      this.$upload('/single/single/upload', formData).then(res => {//将formDate文件上传到阿里云服务器，会返回图片地址
        console.log(res,111111);
        let str = res.response_data[0];
        this.uploadImages.push(str);//将图片地址存入一个数组中
      });

      setTimeout(() => {
        file.status = 'failed';
        file.message = '上传失败';
      }, 1000);
    },
    onSubmit(values) {
      console.log('submit', values);
    },
  },
}
</script>
<style lang="scss" scoped>
  .form {
    margin: 8px 0;
  }
  .btn-group {
    display: flex;
    .van-button {
      margin: 8px;
      flex: 1;
    }
  }
</style>